/* NAVBAR FROM BOOTSTRAP */
a.navbar-brand {
  float: left;
  height: 50px;
  margin-top: -15px;
  padding: 0 15px;
  font-size: 18px;
  line-height: 20px;
  text-decoration: none;
  color: black;
  font-family: cursive;
  font-weight: 700;
}

nav.main-navigation {
    position: relative;
}

nav.main-navigation ul.nav-list {
    margin: 0;
    padding: 0;
    list-style: none;
    position: relative;
    text-align: right;
}

.nav-list li.nav-list-item {
    display: inline-block;
    line-height: 40px;
    margin-left: 30px;
    margin-top: 15px 
}

a.nav-link {
    text-decoration: none;
    font-size: 18px;
    font-family: 'Karla', sans-serif;
    font-weight: 500;
    cursor: pointer;
    position: relative;
    color: black;
}

@-webkit-keyframes FadeIn {
    0% {
        opacity: 0;
        -webkit-transition-duration: 0.8s;
        transition-duration: 0.8s;
        -webkit-transform: translateY(-10px);
        transform: translateY(-10px);
    }


    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
        transform: translateY(0);
        pointer-events: auto;
        -webkit-transition: cubic-bezier(0.4, 0, 0.2, 1);
        transition: cubic-bezier(0.4, 0, 0.2, 1);
    }
}

@keyframes FadeIn {
    0% {
        opacity: 0;
        -webkit-transition-duration: 0.8s;
        transition-duration: 0.8s;
        -webkit-transform: translateY(-10px);
        transform: translateY(-10px);
    }


    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
        transform: translateY(0);
        pointer-events: auto;
        -webkit-transition: cubic-bezier(0.4, 0, 0.2, 1);
        transition: cubic-bezier(0.4, 0, 0.2, 1);
    }
}

.nav-list li {
    -webkit-animation: FadeIn 1s cubic-bezier(0.65, 0.05, 0.36, 1);
            animation: FadeIn 1s cubic-bezier(0.65, 0.05, 0.36, 1);
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
}

.nav-list li:nth-child(1) {
    -webkit-animation-delay: .3s;
            animation-delay: .3s;
}

.nav-list li:nth-child(2) {
    -webkit-animation-delay: .6s;
            animation-delay: .6s;
}

.nav-list li:nth-child(3) {
    -webkit-animation-delay: .9s;
            animation-delay: .9s;
}

.nav-list li:nth-child(4) {
    -webkit-animation-delay: 1.2s;
            animation-delay: 1.2s;
}

.nav-list li:nth-child(5) {
    -webkit-animation-delay: 1.5s;
            animation-delay: 1.5s;
}

/* BOOSTRAP NAVBAR ANIMATION */
/*Estos dos son para hacer el efecto del titulo entrando hacia abajo */
@keyframes fadeInUp {
    from {
        -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0)
    }

    to {
        -webkit-transform: translate3d(0, 40px, 0);
                transform: translate3d(0, 40px, 0);
        opacity: 1
    }
}

@-webkit-keyframes fadeInUp {
    from {
        -webkit-transform: translate3d(0, 0, 0);
                transform: translate3d(0, 0, 0)
    }

    to {
        -webkit-transform: translate3d(0, 40px, 0);
                transform: translate3d(0, 40px, 0);
        opacity: 1
    }
}

.animated1 {
    animation-duration: 1s;
    animation-fill-mode: both;
    -webkit-animation-duration: 1s;
    -webkit-animation-fill-mode: both
}

.animatedFadeInUp {
    opacity: 0
}

.fadeInUp {
    opacity: 0;
    animation-name: fadeInUp;
    -webkit-animation-name: fadeInUp;
}

